.main__userprofile {
  width: 20%;
  padding: 0 10px;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  position: relative;
}

.profile__card {
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  transform: scale(0);
  animation-name: showIn;
  animation-duration: 0.2s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-timing-function: cubic-bezier(0.17, 0.67, 0.12, 1.53);
  animation-fill-mode: both;
  animation-delay: 0.3s;
  margin-bottom: 15px;
}

.profile__card.user__profile__image {
  padding: 20px;
  background-color: grey;
}

.profile__card .profile__image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
}

.profile__card .profile__image img {
  object-fit: cover;
  max-width: 100%;
}

.profile__card h4 {
  margin-bottom: 0;
}

.profile__card p {
  margin-top: 10px;
  font-size: 14px;
  color: #acb3b9;
}

.profile__card .card__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  padding: 15px 20px;
}

.profile__card .card__header h5 {
  margin: 0;
}

.profile__card .card__header i {
  font-size: 20px;
}

.card__content {
  border-top: 1px solid #ebe7fb;
  overflow: auto;
  padding: 15px 20px;
  color: #6d6a6a;
  font-size: 14px;
  display: none;
}

.profile__card.open .card__content {
  display: block;
}

.profile__card.open .card__header i:before {
  content: "\f106";
}

@media (max-width: 768px) {
  .main__userprofile {
    width: 100%;
    padding: 0;
  }
}
