.nav {
    max-width: 100px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 0 25px;
    padding-top: 30px;
  }
  .nav img {
    display: block;
    max-width: 100%;
    aspect-ratio: 3/2;
    cursor: pointer;
    object-fit: contain;
    mix-blend-mode: color-burn;
   
  }